<template>
  <div class="enterprise">
    <h3>{{name}}
      <el-button round class="en-btn" @click="exportTable">
        报表导出
      </el-button>
    </h3>
    <ol class="en-report">
      <li>已完成上报：&nbsp;&nbsp;<span class='en-report-count'>{{theme.success_num_3}} 家</span></li>
      <li>待审核：&nbsp;&nbsp;<span class='en-report-count'>{{theme.doing_num_2}} 家</span></li>
      <li>未上报：&nbsp;&nbsp;<span class='en-report-count'>{{theme.submit_num_1}} 家</span></li>
    </ol>
    <h6>注：以下数据为已完成的数据汇总</h6>
    <el-table
      :data="tableData"
      height="550"
      :header-cell-style="rowClass"
      border
      style="width: 100%">
      <el-table-column
        prop="county"
        label="区县"
        width="100">
        <template slot-scope="scope">
          <span>{{scope.row.county | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="reportCount"
        label="上报企业数（家）"
        width="150">
        <template slot-scope="scope">
          <span>{{scope.row.reportCount | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="enterpriseOutputValue"
        label="企业产值（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.enterpriseOutputValue | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="enterpriseBusinessIncome"
        label="营业收入（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.enterpriseBusinessIncome | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="investmentAmount"
        label="投资额（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.investmentAmount | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="profitAndTax"
        label="利税（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.profitAndTax | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="rdinvestment"
        label="研发投入（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.rdinvestment | numLine}}</span>
        </template>
      </el-table-column>
    </el-table>
    <div class="form-submit submit-box">
      <el-button type="primary" round plain size="small" @click="goBack">返回</el-button>
    </div>
  </div>
</template>
<script>
  import { jPaging } from '../../../components'
  import { dataManage as api } from '../../../api'
  export default {
    name: 'highAndOffice',
    data() {
      return {
        name: '',
        theme: {
          success_num_3: 0,
          doing_num_2: 1,
          submit_num_1: 2
        },
        tableData: [],
        baseUrl: ''
      }
    },
    methods: {
      // 报表导出
      exportTable() {
        location.href = this.baseUrl + '/useReportInfo/DetailBycountyExport?reportFormName=' + this.name
      },
      init() {
        const params = {
          reportFormName: this.name
        }
        api
          .getDetailByCounty(params)
          .then(rtn => {
            this.loading = false
            this.tableData = rtn.data
            this.theme.success_num_3 = rtn.data[0].reportCount
            this.theme.doing_num_2 = rtn.data[0].uncheckCount
            this.theme.submit_num_1 = rtn.data[0].unreportcount
            console.log(rtn)
            // this.total = rtn.data.total
          })
          .catch(err => {
            this.loading = false
            this.pageState = err
          })
      },
      rowClass({ row, rowIndex }) {
        return 'background:#e7ecf9;color:#000'
      },
      goBack() {
        history.go(-1)
      }
    },
    mounted() {
      this.name = this.$route.query.name
      this.baseUrl = window.api_host
      this.init()
    },
    components: {
      jPaging
    }
  }
</script>
<style lang="scss" scoped>
  .enterprise {
    padding:20px;
    h3 {
      padding: 10px 0;
      text-align: right;
      font-weight: 600;
      font-size: 16px;
      .en-btn {
        margin-left: 350px;
        color: #1458ec;
      }
    }
    h6 {
      color: #777777;
      height: 30px;
      line-height: 30px;
    }
    .en-report {
      overflow: hidden;
      color: #777777;
      padding: 20px 0;
      li {
        width: 33%;
        float: left;
      }
      .en-report-count {
        color: #000;
      }
    }
  }
  .submit-box{
    margin-top:20px;
    text-align: center;
  }
</style>



