<template>
  <div>
    <div class="header">
      <el-form :inline="true" label-width="100px">
        <el-form-item label="填报开始时间">
          <el-col :span="11">
            <el-date-picker
              type="date" placeholder="选择日期"
              :picker-options="pickerBeginDateBefore"
              style="width: 150px"
              size="small"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="queryParams.newtimeStart"
            ></el-date-picker>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-date-picker
              type="date"
              size="small"
              :picker-options="pickerBeginDateAfter"
              placeholder="选择日期"
              style="width: 150px;"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="queryParams.newtimeEnd"
            ></el-date-picker>
          </el-col>
        </el-form-item>
      </el-form>
      <div class="form-submit">
        <el-button type="primary" size="small" round @click="queryList">查询</el-button>
        <el-button type="primary" size="small" round plain @click="clear">重置</el-button>
      </div>
    </div>
    <div class="con">
      <div class="table-bor" v-loading='loading'>
        <el-table
          :data="tableData"
          style="width: 100%">
          <el-table-column
            prop="report_form_name"
            label="表单名称"
            width="260" :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <span>{{scope.row.report_form_name | numLine}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="unreacted_num_0124"
            label="未上报企业数"
            width="220" :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <span>{{scope.row.unreacted_num_0124 | numLine}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="creat_time"
            label="填报开始时间">
            <template slot-scope="scope">
              <span>{{scope.row.creat_time | dateFormat}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="endTime"
            label="填报结束时间">
            <template slot-scope="scope">
              <span>--</span>
              <!--<span>{{scope.row.endTime | dateFormat}}</span>-->
            </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label="操作">
            <template slot-scope="scope">
              <router-link :to="{path: '/dataManage/look/enterViewDetail', query: {name: scope.row.report_form_name}}">
                <el-button type="text" size="small"><span class="text-d">查看</span></el-button>
              </router-link>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="paging2">
        <p>
        共<span>{{total}}</span>条
        </p>
        <!--<j-paging :initPageSize="pageSize" :initPageIndex="pageIdx" :initTotalCount="total"-->
        <!--@pageChanged="query"></j-paging>-->
      </div>
    </div>
  </div>
</template>
<script>
  import {jPaging} from '../../../components'
  import { dataManage as api } from '../../../api'
  export default {
    name: '',
    data() {
      return {
        loading: false,
        // pageSize: 10,
        // pageIdx: 1,
        total: 0,
        tableData: [],
        queryParams: {
          newtimeStart: '', // 开始时间
          newtimeEnd: '' // 结束时间
        },
        pickerBeginDateBefore: {
          disabledDate: (time) => {
            let beginDateVal = this.queryParams.newtimeEnd
            if (beginDateVal) {
              return time.getTime() > new Date(beginDateVal)
            } else {
              return time.getTime() > Date.now()
            }
          }
        },
        pickerBeginDateAfter: {
          disabledDate: (time) => {
            let beginDateVal = this.queryParams.newtimeStart
            if (beginDateVal) {
              return time.getTime() < new Date(beginDateVal) || time.getTime() > Date.now()
            } else {
              return time.getTime() > Date.now()
            }
          }
        }
      }
    },
    methods: {
      //  查询
      queryList() {
        this.query(1)
      },
      //  重置
      clear() {
        this.queryParams = {
          newtimeStart: '', // 开始时间
          newtimeEnd: '' // 结束时间
        }
        this.query(1)
      },
      query(pageIndex = 1) {
        const firstJson = {
          // pageNum: pageIndex,
          // pageSize: 10,
          StartTime: this.queryParams.newtimeStart, // 开始时间
          EndTime: this.queryParams.newtimeEnd // 结束时间
        }
        // this.pageIdx = pageIndex
        this.loading = true
        api
          .getStatusNumByReport(firstJson)
          .then(rtn => {
            this.loading = false
            this.tableData = rtn.data
            this.total = this.tableData.length
          })
          .catch(err => {
            this.loading = false
            this.pageState = err
          })
      }
    },
    components: {
      jPaging
    },
    mounted() {
      this.query()
    }
  }
</script>
<style scoped lang="scss">
  .header{
    width: 100%;
    position: relative;
  }
  .el-form-item{
    margin-right: 80px;
  }
  .form-submit {
    position: absolute;
    right: 0;
    bottom: 30px;
    .el-button--small, .el-button--small.is-round{
      padding: 9px 30px;
    }
  }
  .con{
    margin-top: 10px;
    .table-bor {
      border-radius: 5px 5px 0 0;
      overflow: hidden;
    }

    .text-d {
      text-decoration: underline;
    }

    .paging2 {
      margin-top: 30px;
      & > p {
        float: left;
        font-size: 14px;
        color: #959595;
        position: relative;
        span {
          color: #ff9603;
          margin: 0 3px;
        }
      }
      & > div {
        float: right;
      }
    }
  }
</style>



