<template>
  <div class="enterprise">
    <h3>{{name}}
      <el-button type="primary" round plain size="small" class="en-btn" @click="exportTable">
        报表导出
      </el-button>
    </h3>
    <ol class="en-report">
      <li>已完成上报：&nbsp;&nbsp;<span class='en-report-count'>{{theme.success_num_3}} 家</span></li>
      <li>待审核：&nbsp;&nbsp;<span class='en-report-count'>{{theme.doing_num_2}} 家</span></li>
      <li>未上报：&nbsp;&nbsp;<span class='en-report-count'>{{theme.submit_num_1}} 家</span></li>
    </ol>
    <h6>注：以下数据为已完成的数据汇总</h6>
    <el-table
      :data="tableData"
      height="550"
      :header-cell-style="rowClass"
      border
      style="width: 100%">
      <el-table-column
        prop="gov_cname"
        label="区县"
        width="120">
        <template slot-scope="scope">
          <span>{{scope.row.gov_cname  | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="success_num_3"
        label="上报企业数（家）"
        width="150">
        <template slot-scope="scope">
          <span>{{scope.row.success_num_3  | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="enterpriseOutputValue_sum "
        label="企业产值（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.enterpriseOutputValue_sum  | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="enterpriseBusinessIncome_sum "
        label="营业收入（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.enterpriseBusinessIncome_sum  | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="investmentAmount_sum"
        label="投资额（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.investmentAmount_sum  | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="profitAndTax_sum"
        label="利税（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.profitAndTax_sum  | numLine}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="rdinvestment_sum"
        label="研发投入（万元）">
        <template slot-scope="scope">
          <span>{{scope.row.rdinvestment_sum  | numLine}}</span>
        </template>
      </el-table-column>
    </el-table>
     <div class="form-submit submit-box">
      <el-button type="primary" round plain size="small" @click="goBack">返回</el-button>
    </div>
  </div>
</template>
<script>
import { jPaging } from '../../../components'
import { dataManage as api } from '../../../api'
export default {
  name: 'highAndOffice',
  data() {
    return {
      name: '',
      theme: {
        success_num_3: 0,
        doing_num_2: 0,
        submit_num_1: 0
      },
      tableData: [],
      baseUrl: ''
    }
  },
  methods: {
    // 报表导出
    exportTable() {
      location.href =
        this.baseUrl + '/useReportInfo/exportExcel?ReportName=' + this.name
    },
    init() {
      const params = {
        ReportName: this.name
      }
      api
        .getGroupbygov(params)
        .then(rtn => {
          this.tableData = rtn.data
        })
        .catch(err => {
          this.pageState = err
        })
      // 企业数据查看_全市企业数据查看详情上报状态数量统计
      const params2 = {
        ReportName: this.name
      }
      api
        .getStatusNum(params2)
        .then(rtn => {
          this.theme.success_num_3 = rtn.data.success_num_3
          this.theme.doing_num_2 = rtn.data.doing_num_2
          this.theme.submit_num_1 = rtn.data.unreacted_num_0124
        })
        .catch(err => {
          this.loading = false
          this.pageState = err
        })
    },
    rowClass({ row, rowIndex }) {
      return 'background:#e7ecf9;color:#000'
    },
    goBack() {
      history.go(-1)
    }
  },
  mounted() {
    this.name = this.$route.query.name
    this.baseUrl = window.api_host
    this.init()
  },
  components: {
    jPaging
  }
}
</script>
<style lang="scss" scoped>
.enterprise {
  padding: 20px;
  h3 {
    padding: 10px 0;
    text-align: right;
    font-weight: 600;
    font-size: 16px;
    .en-btn {
      margin-left: 350px;
      color: #1458ec;
    }
  }
  h6 {
    color: #777777;
    height: 30px;
    line-height: 30px;
  }
  .en-report {
    overflow: hidden;
    color: #777777;
    padding: 20px 0;
    li {
      width: 33%;
      float: left;
    }
    .en-report-count {
      color: #000;
    }
  }
  .submit-box {
    margin-top: 20px;
    text-align: center;
  }
}
</style>



