<template>
  <div class="reg-detail">
    <h3>{{name}}
      <el-button type="primary" round plain size="small" class="en-btn" @click="exportTable">
        报表导出
      </el-button>
    </h3>
    <ol class="en-report">
      <li>状态：&nbsp;&nbsp;
        <span v-if="status==0" class="stateClass1">未填报</span>
        <span v-if="status==1" class="stateClass2">待上报</span>
        <span v-if="status==2" class="stateClass3">待审核</span>
        <span v-if="status==3" class="stateClass5">已完成</span>
        <span v-if="status==4" class="stateClass4">打回-待上报</span>
      </li>
      <li v-if="status==2" >最新上报时间：&nbsp;&nbsp;<span class='en-report-count'>{{endDate | dateFormat}}</span></li>
      <li v-if="status==3" >完成时间：&nbsp;&nbsp;<span class='en-report-count'>{{endDate | dateFormat}}</span></li>
    </ol>
     <el-table
      :data="tableData"
      stripe
      border
      style="width: 100%">
      <el-table-column
        prop="date"
        label="县(市)区">
         <template slot-scope="scope">{{scope.row[0]}}</template>
      </el-table-column>
      <el-table-column label="人才招引">
        <el-table-column
          prop="name"
          label="国家千人计划（个）">
          <template slot-scope="scope">{{scope.row[1]}}</template>
        </el-table-column>
          <el-table-column
            prop="province"
            label="国家万人计划（个）">
            <template slot-scope="scope">{{scope.row[2]}}</template>
          </el-table-column>
          <el-table-column
            prop="city"
            label="省双创团队（个）">
            <template slot-scope="scope">{{scope.row[3]}}</template>
          </el-table-column>
      </el-table-column>
    </el-table>
    <div class="form-submit">
      <el-button type="primary" round size="small" @click="pass" v-if="status==2">通过</el-button>
      <el-button type="warning" round size="small" v-if="status==2" @click="frame()">打回</el-button>
      <el-button type="primary" round plain size="small" @click="back">返回</el-button>
    </div>
    <j-repulsee @repulse="repulse" @clone="centerDialogVisible = false" :centerDialogVisible="centerDialogVisible" :repulseId="repulseId"></j-repulsee>
  </div>
</template>
<script>
  import { jPaging, jRepulsee } from '../../../components'
  import { dataManage as api } from '../../../api'
  export default {
    name: 'highAndOffice',
    data() {
      return {
        centerDialogVisible: false,
        name: '',
        id: '',
        endDate: '',
        status: '',
        tableData: [],
        baseUrl: '',
        repulseId: ''
      }
    },
    methods: {
      back() {
        this.$router.back()
      },
      exportTable() {
        console.error(this.baseUrl + '/dataUploadData/exportExcel?id=7')
        location.href = this.baseUrl + '/dataUploadData/exportExcel?id=' + this.id
      },
      frame() {
        this.repulseId = this.id
        this.centerDialogVisible = true
      },
      repulse() {
        // this.status = 1
        this.centerDialogVisible = false
        // this.$router.replace({
        //   path: '/dataManage/look/regulatorydetail',
        //   query: {id: this.id, name: this.name, status: this.status, endDate: this.endDate}
        // })
        this.back()
      },
      pass() {
        this.confirmTxt('是否确定通过').then(res => {
          let that = this
          let params = {tableId: that.id, status: 3, optUser: that.realName}
          api.report(params).then(res => {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-icon-correct"></i><span>' + res.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {
                this.back()
                // that.status = 3
                // that.$router.replace({
                //   path: '/dataManage/look/regulatorydetail',
                //   query: {id: that.id, name: that.name, status: that.status, endDate: that.endDate}
                // })
              }
            })
          }).catch(err => {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {}
            })
          })
        }).catch(res => {})
      },
      init() {
        let that = this
        let params = {id: that.id}
        api.getGetTable(params).then(res => {
          if (res.data.data) {
            that.tableData = JSON.parse(res.data.data)
          } else {
            that.tableData = []
          }
        }).catch(err => {
          that.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
        })
      },
      rowClass({ row, rowIndex }) {
        return 'background:#e7ecf9;color:#000'
      }
    },
    mounted() {
      this.id = this.$route.query.id
      this.name = this.$route.query.name
      this.endDate = this.$route.query.lastTime
      this.status = this.$route.query.status
      this.baseUrl = window.api_host
      this.init()
    },
    components: {
      jPaging,
      jRepulsee
    }
  }
</script>
<style lang="scss" scoped>
  .reg-detail {
    .form-submit{
      text-align: center;
      padding: 20px;
    }
    padding:20px;
    h3 {
      padding: 10px 0;
      text-align: right;
      font-weight: 600;
      font-size: 16px;
      .en-btn {
        margin-left: 350px;
        color: #1458ec;
      }
    }
    h6 {
      color: #777777;
      height: 30px;
      line-height: 30px;
    }
    .en-report {
      overflow: hidden;
      color: #777777;
      padding: 20px 0;
      li {
        float: left;
        padding-right: 40px;
        height: 30px;
        line-height: 30px;
      }
      .en-report-count {
        color: #000;
      }
    }
    .stateClass1{
    color: #a0a0a0;
    }
    .stateClass2{
      color:#ee8b00;
    }
    .stateClass3{
      color: #1559ec;
    }
    .stateClass4{
      color: #ef8b00;
    }
    .stateClass5{
      color: #28b266;
    }
  }
</style>



