<template>
  <div class="local_list">
    <el-form :inline="true" label-width="100px" ref="filterData" :model="filterData">
      <el-form-item label="填报开始时间">
        <el-col :span="11">
          <el-form-item prop="qStartTime">
            <el-date-picker type="date" :picker-options="pickerBeginDateBefore" v-model="filterData.qStartTime" value-format="yyyy-MM-dd HH:mm:ss" @change="getSTime" placeholder="选择日期" style="width: 150px" size="small"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2">&nbsp;</el-col>
        <el-col :span="11">
          <el-form-item prop="qEndTime">
            <el-date-picker type="date" :picker-options="pickerBeginDateAfter" v-model="filterData.qEndTime" value-format="yyyy-MM-dd HH:mm:ss" @change="getETime" placeholder="选择日期" style="width: 150px;" size="small"></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <!-- <el-button type="primary" size="small" round @click="filterQuery">查询</el-button>
      <el-button type="primary" size="small" round plain @click="resetForm">重置</el-button> -->
      <el-button type="primary" size="small" round @click="filterQuery">查询</el-button>
      <el-button type="primary" size="small" round plain @click="clearForm">重置</el-button>
    </div>
    <el-table :data="tableData" stripe border style="width: 100%" v-loading="loading">
      <el-table-column prop="reportFormName" align="left" label="表单名称" show-overflow-tooltip width="280"></el-table-column>
      <el-table-column prop="unreport" label="未上报企业" show-overflow-tooltip width="150"></el-table-column>
      <el-table-column align="center" label="填报开始时间">
        <template slot-scope="scope">
          <span>{{scope.row.creatTime | dateFormat}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" width="120" label="填报结束时间">
        <template slot-scope="scope">
          <span>{{scope.row.releasetime | dateFormat}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="230" prop="id">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="localView(scope.row.reportFormName)" class="p0"><span class="text-d">查看</span></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination clearFix">
      <div class="pagination-num">
        共
        <span class="yellow"> {{filterData.total}} </span>条
      </div>
      <div class="pagination-nav">
        <j-paging class="paging" :initPageSize="filterData.pageSize" :initPageIndex="filterData.pageNum" :initTotalCount="filterData.total" @pageChanged="query"></j-paging>
      </div>
    </div>
  </div>
</template>
<script>
import { dataManage as api } from '../../../api'
import { jPaging } from '../../../components'
export default {
  name: 'infopolicy',
  data() {
    return {
      loading: false,
      value: '',
      tableData: [],
      filterData: {
        pageSize: 10,
        pageNum: 1,
        total: 0,
        qStartTime: '',
        qEndTime: ''
      },
      pickerBeginDateBefore: {
        disabledDate: (time) => {
          let beginDateVal = this.filterData.qEndTime
          if (beginDateVal) {
            return time.getTime() > new Date(beginDateVal)
          } else {
            return time.getTime() > Date.now()
          }
        }
      },
      pickerBeginDateAfter: {
        disabledDate: (time) => {
          let beginDateVal = this.filterData.qStartTime
          if (beginDateVal) {
            return time.getTime() < new Date(beginDateVal) || time.getTime() > Date.now()
          } else {
            return time.getTime() > Date.now()
          }
        }
      }
    }
  },
  methods: {
    localView(row) {
      this.$router.push({name: 'localViewData', params: {title: row}})
    },
    getSTime(val) {
      this.filterData.qStartTime = val
    },
    getETime(val) {
      this.filterData.qEndTime = val
    },
    clearForm() {
      this.filterData.qStartTime = ''
      this.filterData.qEndTime = ''
      this.filterQuery()
    },
    getData() {
      this.loading = true
      api.getListByCC(this.filterData).then(res => {
        this.tableData = res.data.list
        this.filterData.total = res.data.total
        this.filterData.pageSize = res.data.pageSize
        this.filterData.pageNum = res.data.pageNum
        this.loading = false
      }).catch(res => {
        this.loading = false
      })
    },
    filterQuery() {
      this.filterData.pageNum = 1
      this.getData()
    },
    query(page) {
      let that = this
      that.filterData.pageNum = page
      that.getData()
    },
    resetForm() {
      this.filterData.pageNum = 1
      this.$refs['filterData'].resetFields()
      this.getData()
    }
  },
  components: {
    jPaging
  },
  created() {
    let that = this
    if (localStorage.filterData) {
      that.filterData = JSON.parse(localStorage.filterData)
    }
    this.getData()
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'localViewData') {
      localStorage.filterData = JSON.stringify(this.filterData)
      from.meta.keepAlive = true
    } else {
      localStorage.removeItem('filterData')
      from.meta.keepAlive = false
    }
    next()
  }
}
</script>
<style lang="scss" scoped>
.table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;
  }

  .text-d {
    text-decoration: underline;
  }

  .paging2 {
    margin-top: 30px;
    & > p {
      float: left;
      font-size: 14px;
      color: #959595;
      position: relative;
      span {
        color: #ff9603;
        margin: 0 3px;
      }
    }
    & > div {
      float: right;
    }
  }
.local_list {

  .form-submit {
    text-align: center;
    margin-bottom: 30px;
    border-bottom: dotted 1px #ccc;
    padding-bottom: 30px;
  }
  .line{
    text-align:center
  }
   .pagination{
      margin-top: 30px;
      width: 100%;
      height:40px;
      .pagination-num{
        float: left;
        .yellow{
          color:#ff9603;
          font-weight:bold
        }
      }
      .pagination-nav{
        float: right;
      }
    }
}
</style>



