<template>
  <div class="m30">
    <div class="table-bor">
      <div class="clearFix form-submit table-title">
        <span>{{ name }}</span>
        <el-button type="primary" round plain size="small" class="fr" @click="exportExcel">报表导出</el-button>
        <el-upload :show-file-list="false" class="fr mr20"  v-show="showBtn"
                   :action="baseUrl+'/dataUpLoadController/importExcel'"
                   :on-success="(res)=>{return importExcel(res)}">
          <el-button type="primary" round plain size="small">报表导入</el-button>
        </el-upload>
      </div>
      <jBackList :backlist="backList"></jBackList>
      <el-table :data="tableData" style="width: 100%;" class="edit-table">
        <el-table-column prop="county" label="县（市）区" width="120">
          <template slot-scope="scope">{{scope.row[0]}}</template>
        </el-table-column>
        <el-table-column label="知识产权" align="center">
          <el-table-column prop="other" label="专利申请量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[1]}}</span>
              <el-input v-model="scope.row[1]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="专利申请量增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[2]}}</span>
              <el-input v-model="scope.row[2]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="专利授权量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[3]}}</span>
              <el-input v-model="scope.row[3]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="专利授权量增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[4]}}</span>
              <el-input v-model="scope.row[4]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="发明专利申请量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[5]}}</span>
              <el-input v-model="scope.row[5]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="发明专利申请量增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[6]}}</span>
              <el-input v-model="scope.row[6]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="企业专利申请量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[7]}}</span>
              <el-input v-model="scope.row[7]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="企业专利申请量增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[8]}}</span>
              <el-input v-model="scope.row[8]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="企业专利授权量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[9]}}</span>
              <el-input v-model="scope.row[9]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="企业专利授权量增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[10]}}</span>
              <el-input v-model="scope.row[10]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="万人发明专利拥有量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[11]}}</span>
              <el-input v-model="scope.row[11]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="万人发明专利拥有量增幅(%，与上年末比较)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[12]}}</span>
              <el-input v-model="scope.row[12]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="PCT专利申请量(件)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[13]}}</span>
              <el-input v-model="scope.row[13]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>

        <el-table-column label="创新要素" align="center">
          <el-table-column prop="other" label="知识产权质押企业数(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[14]}}</span>
              <el-input v-model="scope.row[14]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="知识产权质押贷款额(万元)" width="200">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[15]}}</span>
              <el-input v-model="scope.row[15]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>

      </el-table>
    </div>
    <div class="form-submit submit-box" v-if="showBtn">
      <el-button type="primary" round size="small" @click="save">保存</el-button>
      <el-button type="primary" round plain size="small" @click="cancel">取消</el-button>
    </div>
    <div class="form-submit submit-box" v-else>
      <el-button type="primary" round plain size="small" @click="goBack">返回</el-button>
    </div>
  </div>
</template>
<script>
  import { dataManage as api } from '../../../api'
  import { jBackList } from '../../../components'
  export default {
    name: 'editKnowledgeOffice',
    data() {
      return {
        backList: [],
        tableData: [],
        params: [],
        baseUrl: '',
        id: '',
        relativeId: '',
        flag: '',
        showBtn: true,
        name: ''
      }
    },
    mounted() {
      this.params = this.$route.query
      this.name = this.$route.query.data.name
      this.baseUrl = window.api_host
      if (this.$route.query.op === 'look') {
        this.showBtn = false
      }
      this.query()
      this.getBackList()
    },
    methods: {
      query() {
        api
          .getEditTable(this.params)
          .then(rtn => {
            if (rtn.data === '') {
              this.id = 0
              this.relativeId = this.params.id
              this.flag = 0
            } else {
              let rtnData = JSON.parse(rtn.data.data)
              this.id = rtn.data.id
              this.relativeId = rtn.data.relativeId
              this.tableData = rtnData
              this.flag = 1
            }
          })
          .catch(err => {
            this.pageState = err
          })
      },
      importExcel(res) {
        this.tableData = res.data
      },
      exportExcel() {
        location.href = this.baseUrl + '/dataUpLoadController/exportExcel?id=' + this.relativeId
      },
      save() {
        let that = this
        if (this.tableData.length === 0) {
          this.$message.error('请导入报表后再提交！')
          return
        }
        let arr = JSON.stringify(this.tableData)
        let params = {
          relateid: this.relativeId,
          id: this.id,
          data: arr,
          flag: this.flag
        }
        api
          .saveExcel(params)
          .then(rtn => {
            if (rtn.code === 1) {
              that.cancel()
            }
          })
          .catch(err => {
            this.pageState = err
          })
      },
      cancel() {
        this.$router.push('/dataManage/fillingOffice/knowledgeOffice')
      },
      goBack() {
        this.$router.push('/dataManage/fillingOffice/knowledgeOffice')
      },
      getBackList() {
        let params = {tableId: this.$route.query.id}
        api.getReportList(params).then(res => {
          this.backList = res.data.list
        }).catch(res => {
        })
      }
    },
    components: {
      jBackList
    }
  }
</script>
<style scoped lang="scss">
  .m30{
    margin:30px;
  }
  .mr20{
    margin-right:20px;
  }
  .table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;

  .table-title{
    padding-bottom:20px;
    margin-bottom:10px;
    text-align:center;

  span{
    line-height:32px;
    font-size:16px;
    color:#333;
  }
  }
  }
  .submit-box{
    text-align: center;
    margin-top: 30px;
  }
  /deep/ .el-table thead.is-group th{
    border-right:solid 1px #fff;
  }
  /deep/ .el-table th{
    border-bottom:solid 1px #fff;
  }
</style>



