<template>
  <div class="enterpriseAudit">
    <div class="header">
      <el-form :inline="true" label-width="100px">
        <el-form-item label="填报企业"  class="input-padding">
          <el-input placeholder="请输入填报企业" v-model="queryParams.title" size="small" style="width: 196px;"></el-input>
        </el-form-item>
        <el-form-item label="填报开始时间">
          <el-col :span="11">
            <el-date-picker
              type="date" placeholder="选择日期"
              style="width: 150px"
               size="small" 
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="queryParams.newtimeStart"
              :picker-options="pickerBeginDateBefore"
            ></el-date-picker>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-date-picker
              type="date"
               size="small" 
              placeholder="选择日期"
              style="width: 150px;"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="queryParams.newtimeEnd"
              :picker-options="pickerBeginDateAfter"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="状态"  class="input-padding">
          <el-select v-model="queryParams.status" placeholder="请选择"  size="small"  style="width: 196px;">
            <el-option
              v-for="item in stateList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div class="form-submit">
        <el-button type="primary" size="small" round @click="queryList">查询</el-button>
        <el-button type="primary" size="small" round plain @click="clear">重置</el-button>
      </div>
    </div>
    <div class="con">
      <div class="table-bor" v-loading='loading'>
        <el-table
          :data="tableData"
          style="width: 100%" :show-overflow-tooltip="true">
          <el-table-column
            prop="reportFormName"
            label="表单名称"
            width="260">
          </el-table-column>
          <el-table-column
            prop="enterpriseName"
            label="填报企业"
            width="220" :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <span>{{scope.row.enterpriseName | numConvert}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="creatTime"
            label="填报开始时间" width="170">
            <template slot-scope="scope">
              <span>{{scope.row.creatTime | dateFormat}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="endTime"
            label="填报结束时间">
            <template slot-scope="scope">
              <span>--</span>
              <!--<span>{{scope.row.endTime | dateFormat}}</span>-->
            </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label="状态"
            width="100">
            <template slot-scope="scope">
              <span v-if="scope.row.status==0" class="stateClass1">未填报</span>
              <span v-if="scope.row.status==1" class="stateClass2">待上报</span>
              <span v-if="scope.row.status==2" class="stateClass3">待审核</span>
              <span v-if="scope.row.status==3" class="stateClass5">已完成</span>
              <span v-if="scope.row.status==4" class="stateClass4">打回-待上报</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label="操作">
            <template slot-scope="scope">
              <router-link @click.native="adopt(scope.row.id)":to="{}" v-if="scope.row.status === 2">
                <el-button type="text" size="small"><span class="text-d">通过</span></el-button>
              </router-link>
              <router-link @click.native="frame(scope.row.id)" :to="{}" v-if="scope.row.status === 2">
                <el-button type="text" size="small"><span class="text-d">打回</span></el-button>
              </router-link>
              <router-link :to="{path:'/dataManage/edit/enterpriseAudit',query:{id:scope.row.id}}" v-if="scope.row.status === 2 || scope.row.status === 3 ">
                <el-button type="text" size="small"><span class="text-d">查看</span></el-button>
              </router-link>
              <a v-if="scope.row.status === 0 || scope.row.status === 1 || scope.row.status === 4">-</a>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="paging2">
        <p>
          共<span>{{total}}</span>条
        </p>
        <j-paging :initPageSize="pageSize" :initPageIndex="pageIdx" :initTotalCount="total"
                  @pageChanged="query"></j-paging>
      </div>
    </div>
    <j-repulse @repulse="repulse" @clone="centerDialogVisible = false" :centerDialogVisible="centerDialogVisible" :repulseId="repulseId"></j-repulse>
  </div>
</template>
<script>
import {jPaging, jRepulse} from '../../../components'
import { dataManage as api } from '../../../api'
export default {
  name: '',
  data() {
    return {
      centerDialogVisible: false,
      loading: false,
      pageSize: 10,
      pageIdx: 1,
      total: 12,
      tableData: [],
      queryParams: {
        title: '', // 填报企业
        newtimeStart: '', // 开始时间
        newtimeEnd: '', // 结束时间
        status: '' //  状态
      },
      stateList: [
        { label: '未填报', value: 0 },
        { label: '待上报', value: 1 },
        { label: '待审核', value: 2 },
        { label: '已完成', value: 3 },
        { label: '打回-待上报', value: 4 }
      ],
      repulseId: '',

      pickerBeginDateBefore: {
        disabledDate: (time) => {
          let beginDateVal = this.queryParams.newtimeEnd
          if (beginDateVal) {
            return time.getTime() > new Date(beginDateVal)
          } else {
            return time.getTime() > Date.now()
          }
        }
      },
      pickerBeginDateAfter: {
        disabledDate: (time) => {
          let beginDateVal = this.queryParams.newtimeStart
          if (beginDateVal) {
            return time.getTime() < new Date(beginDateVal) || time.getTime() > Date.now()
          } else {
            return time.getTime() > Date.now()
          }
        }
      }
    }
  },
  methods: {
    //  查询
    queryList() {
      this.query(1)
    },
    //  清除
    clear() {
      this.queryParams = {
        title: '', // 填报企业
        newtimeStart: '', // 开始时间
        newtimeEnd: '', // 结束时间
        status: '' //  状态
      }
      this.query(1)
    },
    query(pageIndex = 1) {
      const firstJson = {
        pageNum: pageIndex,
        pageSize: 10,
        enterpriseName: this.queryParams.title, // 企业名称
        qStartTime: this.queryParams.newtimeStart, // 开始时间
        qEndTime: this.queryParams.newtimeEnd, // 结束时间
        status: this.queryParams.status // 状态
      }
      this.pageIdx = pageIndex
      this.loading = true
      api
        .getReportByCounty(firstJson)
        .then(rtn => {
          this.loading = false
          this.tableData = rtn.data.list
          this.total = rtn.data.total
        })
        .catch(err => {
          this.loading = false
          this.pageState = err
        })
    },
    // 打回
    frame(id) {
      this.repulseId = id
      this.centerDialogVisible = true
    },
    // 打回-确定按钮
    repulse() {
      this.centerDialogVisible = false
      this.query(1)
    },
    // 通过
    adopt(id) {
      let params = {
        reportId: id,
        ispass: 0
      }
      api.getInfoapply(params).then(res => {
        if (res.code === 1) {
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {
              this.query(1)
            }
          })
        }
      }).catch(err => {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {
          }
        })
      })
    }
  },
  components: {
    jRepulse,
    jPaging
  },
  mounted() {
    this.query(1)
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'DataAuditView') {
      localStorage.queryParams = JSON.stringify(this.queryParams)
      from.meta.keepAlive = true
    } else {
      localStorage.removeItem('queryParams')
      from.meta.keepAlive = false
    }
    next()
  }
}
</script>
<style scoped lang="scss">
  .header{
    width: 100%;
    position: relative;
  }
  .el-form-item{
    margin-right: 80px;
  }
  /** .form-submit {
    position: absolute;
    right: 0;
    bottom: 30px;
    .el-button--small, .el-button--small.is-round{
      padding: 9px 30px;
    }
  } **/
   .form-submit {
    text-align: center;
    margin-bottom: 30px;
    border-bottom: dotted 1px #ccc;
    padding-bottom: 30px;
  }
  .con{
    margin-top: 10px;
    .table-bor {
      border-radius: 5px 5px 0 0;
      overflow: hidden;
    }

    .text-d {
      text-decoration: underline;
    }

    .paging2 {
      margin-top: 30px;
      & > p {
        float: left;
        font-size: 14px;
        color: #959595;
        position: relative;
        span {
          color: #ff9603;
          margin: 0 3px;
        }
      }
      & > div {
        float: right;
      }
    }
    .stateClass1{
      color: #a0a0a0;
    }
    .stateClass2{
      color:#ee8b00;
    }
    .stateClass3{
      color: #1559ec;
    }
    .stateClass4{
      color: #ef8b00;
    }
    .stateClass5{
      color: #28b266;
    }
  }
   /*/deep/ .cell .el-button--small, .el-button--small.is-round {*/
    /*padding: 0;*/
  /*}*/

</style>



