              <el-input v-model="scope.row[4]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="高新技术产业投资额增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[5]}}</span>
              <el-input v-model="scope.row[5]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="高新技术产业投资额占规模以上工业投资比重(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[6]}}</span>
              <el-input v-model="scope.row[6]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="高新技术产业投资额占规模以上工业投资比重增幅(%)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[7]}}</span>
              <el-input v-model="scope.row[7]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="传统产业技改投入额(万元)" width="190">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[8]}}</span>
              <el-input v-model="scope.row[8]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>

        <el-table-column label="企业创新" align="center">
          <el-table-column label="高新技术企业数量(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[9]}}</span>
              <el-input v-model="scope.row[9]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="高新技术企业数量增幅(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[10]}}</span>
              <el-input v-model="scope.row[10]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="列统高新技术企业数量(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[11]}}</span>
              <el-input v-model="scope.row[11]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="列统高新技术企业数量增幅(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[12]}}</span>
              <el-input v-model="scope.row[12]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="科技型中小企业评价入库数(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[13]}}</span>
              <el-input v-model="scope.row[13]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="科技企业减免税额(万元)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[14]}}</span>
              <el-input v-model="scope.row[14]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="企业研发费用加计扣除数(万元)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[15]}}</span>
              <el-input v-model="scope.row[15]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>

        <el-table-column label="创新要素" align="center">
          <el-table-column label="科技风险贷款企业数(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[16]}}</span>
              <el-input v-model="scope.row[16]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="科技风险贷款投资额(万元)" width="190">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[17]}}</span>
              <el-input v-model="scope.row[17]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="创投机构数量(家)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[18]}}</span>
              <el-input v-model="scope.row[18]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column label="创投规模(万元)" width="180">
            <template slot-scope="scope">
              <span v-if="!showBtn">{{scope.row[19]}}</span>
              <el-input v-model="scope.row[19]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
    <div class="form-submit submit-box" v-if="showBtn">
      <el-button type="primary" round size="small" @click="save">保存</el-button>
      <el-button type="primary" round plain size="small" @click="cancel">取消</el-button>
    </div>
    <div class="form-submit submit-box" v-else>
      <el-button type="primary" round plain size="small" @click="goBack">返回</el-button>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div class="dialog-content">
        <p>请输入原因</p>
        <textarea name="area1"  cols="40" rows="5" v-model='unadoptData.reason'></textarea>
        <p>操作人:<span>{{unadoptData.operator}}</span></p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sureAdpt()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { dataManage as api } from '../../../api'
import { jBackList } from '../../../components'
export default {
  name: 'editHighAndOffice',
  data() {
    return {
      backList: [],
      unadoptData: {
        reason: '',
        status: 4,
        optUser: 'admin'
      },
      centerDialogVisible: false,
      repulse: false,
      theme: {
        status: '',
        time: '2018-02-07 10:09:10',
        adoptFlags: false,
        themeShow: false
      },
      tableData: [],
      params: [],
      baseUrl: '',
      id: '',
      relativeId: '',
      flag: '',
      showBtn: true,
      name: '',
      Authorization: ''
    }
  },
  mounted() {
    this.Authorization = localStorage.getItem('accessToken')
    this.theme.status = this.$route.query.status
    if (this.$route.query.status === 3) {
      this.theme.adoptFlags = false
    } else if (this.$route.query.status === 2) {
      this.theme.adoptFlags = true
    }
    this.params = this.$route.query
    this.name = this.$route.query.name
    this.baseUrl = window.api_host
    if (this.$route.query.op === 'look') {
      this.showBtn = false
      this.query()
    } else if (this.$route.query.op === 'examine') {
      this.showBtn = false
      this.theme.themeShow = true
      this.queryDetial()
    } else {
      this.query()
    }
    this.getBackList()
  },
  methods: {
    sureAdpt() {
      this.centerDialogVisible = false
      this.repulse = true
    },
    adopt() {
      let params = {
        tableId: this.params.id,
        status: 3,
        Authorization: this.Authorization
      }
      let _this = this
      api
        .report(params)
        .then(rtn => {
          if (rtn.code === 1) {
            _this.cancel()
          } else {
            throw new Error('返回数据错误')
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    unadopt() {
      this.centerDialogVisible = true
      this.unadoptData.tableId = this.params.id
      this.unadoptData.Authorization = this.Authorization
    },
    queryDetial() {
      api
        .getDetialTable({
          id: this.$route.query.id,
          Authorization: this.Authorization
        })
        .then(rtn => {
          console.log('queryDetial:', rtn)
          if (rtn.data === '') {
            this.id = 0
            this.relativeId = this.params.id
            this.flag = 0
          } else {
            let rtnData = JSON.parse(rtn.data.data)
            this.id = rtn.data.id
            this.relativeId = rtn.data.relativeId
            this.tableData = rtnData
            this.flag = 1
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    query() {
      api
        .getEditTable(this.params)
        .then(rtn => {
          console.log('query:', rtn)
          if (rtn.data === '') {
            this.id = 0
            this.relativeId = this.params.id
            this.flag = 0
          } else {
            let rtnData = JSON.parse(rtn.data.data)
            this.id = rtn.data.id
            this.relativeId = rtn.data.relativeId
            this.tableData = rtnData
            this.flag = 1
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    importExcel(res) {
      this.tableData = res.data
    },
    exportExcel() {
      location.href =
        this.baseUrl + '/dataUpLoadController/exportExcel?id=' + this.relativeId
    },
    save() {
      let that = this
      if (this.tableData.length === 0) {
        this.$message.error('请导入报表后再提交！')
        return
      }
      let arr = JSON.stringify(this.tableData)
      let params = {
        relateid: this.relativeId,
        id: this.id,
        data: arr,
        flag: this.flag,
        Authorization: this.Authorization
      }
      console.log('save:', params)
      api
        .saveExcel(params)
        .then(rtn => {
          if (rtn.code === 1) {
            that.cancel()
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    cancel() {
      // this.$router.push('/dataManage/fillingOffice/highAndOffice')
      history.go(-1)
    },
    goBack() {
      history.go(-1)
    },
    getBackList() {
      let params = {tableId: this.$route.query.id}
      api.getReportList(params).then(res => {
        this.backList = res.data.list
      }).catch(res => {
      })
    }
  },
  watch: {
    repulse(oldValue, newVal) {
      if (oldValue) {
        api
          .report(this.unadoptData)
          .then(rtn => {
            if (rtn.code === 1) {
              this.cancel()
              console.log('成功修改')
            } else {
              throw new Error('返回数据错误')
            }
          })
          .catch(err => {
            this.pageState = err
          })
      }
    }
  },
  components: {
    jBackList
  }
}
</script>
<style scoped lang="scss">
.high_and_office{
  .back-info{
  padding:30px 130px;
  background: #edf3ff;
  margin-bottom: 10px;
  &:last-child{
    margin-bottom:30px;
  }
  .i-tit{
    font-weight: bolder;
    margin-bottom: 20px;
    word-break:break-all

  }
  .i-time{
    text-align: right;
    color:#7c7d7d
  }
}
}
.m30 {
  margin: 30px;
}
.mr20 {
  margin-right: 20px;
}
.heigh_theme {
  li {
    float: left;
    padding-right: 40px;
    height: 30px;
    line-height: 30px;
  }
}
.table-bor {
  border-radius: 5px 5px 0 0;
  overflow: hidden;

  .table-title {
    padding-bottom: 20px;
    margin-bottom: 10px;
    text-align: center;

    span {
      line-height: 32px;
      font-size: 16px;
      color: #333;
    }
  }
}
.submit-box {
  text-align: center;
  margin-top: 30px;
}
/deep/ .el-table thead.is-group th{
    border-right:solid 1px #fff;
  }
  /deep/ .el-table th{
    border-bottom:solid 1px #fff;
  }
</style>



