<template>
  <div class="user-data-state">
    <div class="page clearFix">
      <div class="p-title"><span v-if="!tableData.reportFormName">-</span>{{tableData.reportFormName | numLine}}</div>
      <div class="p-state">状态：
        <strong class="p-s-res" v-if="tableData.status===2"><span class="primary">待审核</span>最新上报时间 ：</strong>
        <strong class="p-s-res" v-if="tableData.status===3"><span class="success">已完成</span>完成时间 :</strong>
        <span>{{tableData.endTime | dateFormat}}</span>
      </div>
    </div>
    <div v-if="backList!=''">
      <jTitle>打回记录</jTitle>
      <div class="back-info" v-for="item in backList" :key="item.id">
        <p class="i-tit">{{item.approvalOpinion | numLine}}</p>
        <p class="i-time">操作人：
          <span>{{item.approvalUserName | numLine}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.approvalTime | dateFormat}}</p>
      </div>
    </div>
    <div>
      <jTitle>填报信息</jTitle>
      <div class="info clearFix">
        <div class="i-left clearFix">
          <dl class="clearFix">
            <dt>填报企业：</dt>
            <dd>{{tableData.enterpriseName | numLine}}</dd>
          </dl>
          <dl class="clearFix">
            <dt>企业产值：</dt>
            <dd>{{tableData.enterpriseOutputValue | numLine}} 万元</dd>
          </dl>
          <dl class="clearFix">
            <dt>投资额：</dt>
            <dd>{{tableData.investmentAmount | numLine}} 万元</dd>
          </dl>
          <dl class="clearFix">
            <dt>研发投入：</dt>
            <dd>{{tableData.rdinvestment | numLine}} 万元</dd>
          </dl>
        </div>
        <div class="i-right clearFix">
          <dl class="clearFix">
            <dt>营业收入：</dt>
            <dd>{{tableData.enterpriseBusinessIncome | numLine}} 万元</dd>
          </dl>
          <dl class="clearFix">
            <dt>利税：</dt>
            <dd>{{tableData.profitAndTax | numLine}} 万元</dd>
          </dl>
        </div>
      </div>
    </div>
    <div class="form-submit">
      <el-button type="primary" round size="small" v-if="tableData.status===2" @click="adopt(tableData.id)">通过</el-button>
      <el-button type="warning" round size="small" v-if="tableData.status===2" @click="upDateStatus(tableData.id)">打回</el-button>
      <el-button type="primary" round plain size="small" @click="back">返回</el-button>
    </div>
    <j-repulse @repulse="repulse" @clone="centerDialogVisible = false" :centerDialogVisible="centerDialogVisible" :repulseId="repulseId"></j-repulse>
  </div>
</template>
<script>
  import { dataManage as api } from '../../../api'
  import { jTitle, jRepulse } from '../../../components'
  export default {
    data() {
      return {
        id: '',
        tableData: '',
        centerDialogVisible: false,
        repulseId: '',
        backList: ''
      }
    },
    components: {
      jTitle,
      jRepulse
    },
    computed: {
    },
    methods: {
      formatTime() {
        if (this.tableData.creatTime === '') {
          return '-'
        }
        let fdate = new Date(this.tableData.creatTime)
        let year = fdate.getFullYear()
        let month = fdate.getMonth() + 1
        let date = fdate.getDate()
        let hour = fdate.getHours()
        let minute = fdate.getMinutes()
        if (hour === 0) {
          hour = '00'
        }
        if (minute === 0) {
          minute = '00'
        }
        return year + '-' + month + '-' + date + ' ' + hour + ':' + minute
      },
      // 返回
      back() {
        this.$router.back()
      },
      // 通过
      adopt(id) {
        let that = this
        let params = {
          reportId: id,
          ispass: 0
        }
        api.getInfoapply(params).then(res => {
          if (res.code === 1) {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {
                this.$router.push('/dataManage/districtCountyAudits/enterpriseAudit')
              }
            })
          }
        }).catch(err => {
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {
            }
          })
        })
      },
      // 打回
      upDateStatus(id) {
        this.centerDialogVisible = true
        this.repulseId = id
      },
      // 打回-确定
      repulse() {
        this.$router.push('/dataManage/districtCountyAudits/enterpriseAudit')
      },
      // 详情
      selectList() {
        let params = {id: this.$route.query.id}
        this.id = this.$route.query.id
        api.getSelectDetail(params).then(res => {
          this.tableData = res.data
        }).catch(err => {
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
        })
        let params2 = {reportId: this.$route.query.id}
        api.getSelectApplyList(params2).then(res => {
          this.backList = res.data.list
        }).catch(err => {
          this.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
        })
      }
    },
    mounted() {
      this.selectList()
    }
  }
</script>
<style lang="scss" scoped>
  $blue: #1458ec;
  $green:#22ae95;
  .user-data-state{
    padding:30px;
    box-sizing:border-box;
    .page{
      height:28px;
      line-height: 28px;
      margin-bottom: 30px;
      position: relative;
      &:after{
        content:'';
        position: absolute;
        bottom:0;
        right:0;
        left:300px;
        border-bottom:solid 1px #447cfe;
      }
      .p-title{
        width: 340px;
        color:#fff;
        float: left;
        padding:0 10px;
        box-sizing:border-box;
        background: url(../../../assets/images/t-bg.png) no-repeat;
      }
      .p-state{
        color:#777;
        float: right;
        padding-right: 10px;
        box-sizing:border-box;
        .p-s-res{
          span{
            margin-right: 20px;
            font-weight: bolder;
            &.info{color:#9d9d9d}
            &.warning{color:#f2aa48}
            &.primary{color:#1559ec}
            &.success{color:#45bc7a}
          }
        }
      }

    }
    .back-info{
      padding:30px 130px;
      background: #edf3ff;
      margin-bottom: 10px;
      &:last-child{
        margin-bottom:30px;
      }
      .i-tit{
        font-weight: bolder;
        margin-bottom: 20px;

      }
      .i-time{
        text-align: right;
      }
    }
    .info{
      width: 80%;
      margin:auto;
      .i-left{
        width: 50%;
        float: left;
        padding-left: 80px;
        box-sizing:border-box;
      }
      .i-right{
        width: 50%;
        float: left;
        padding-left: 200px;
        box-sizing:border-box;
      }
      dl{
        padding-bottom: 20px;
        width: 100%;
      }
      dt{
        float: left;
        width: 80px;
      }
      dd{
        float: left;
      }
    }
    .form-submit{
      text-align: center;
      border-top:solid 1px #9ebdff;
      padding: 50px 0;
      margin-top:30px;
      box-sizing:border-box;
    }
  }

</style>



