<template>
  <div>
    <div class="table-bor">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column show-overflow-tooltip
                         prop="name"
                         label="表单名称"
                         width="280">
        </el-table-column>
        <el-table-column
          prop="operateSpace"
          label="填报处室"
          width="80">
        </el-table-column>
        <el-table-column
          prop="startDate"
          label="填报开始时间">
          <template slot-scope="scope">
            <span>{{scope.row.startDate | dateFormat}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="endDate"
          label="填报结束时间">
          <template slot-scope="scope">
            <span>--</span>
            <!--<span>{{scope.row.endDate | dateFormat}}</span>-->
          </template>
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.status==0" class="stateClass1">未填报</span>
            <span v-if="scope.row.status==1" class="stateClass2">待上报</span>
            <span v-if="scope.row.status==2" class="stateClass3">待审核</span>
            <span v-if="scope.row.status==3" class="stateClass5">已完成</span>
            <span v-if="scope.row.status==4" class="stateClass4">打回-待上报</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="operation"
          label="操作">
          <template slot-scope="scope">
            <div v-if="scope.row.status===2">
              <el-button type="text" size="small" @click="adopt(scope.row)" class="p0"><span class="text-d">通过</span></el-button>
              <el-button type="text" size="small" @click="unadopt(scope.row)" class="p0"><span class="text-d">打回</span></el-button>
              <el-button type="text" size="small" @click="fillReport(scope.row.id,scope.row.name,scope.row.status,scope.row.lastTime)" class="p0"><span class="text-d">查看</span></el-button>
            </div>
            <div v-else-if="scope.row.status===3">
              <el-button type="text" size="small" @click="fillReport(scope.row.id,scope.row.name,scope.row.status,scope.row.lastTime)" class="p0"><span class="text-d">查看</span></el-button>
            </div>
            <div v-else >
              <el-button type="text" size="small"  class="p0">-</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="paging2">
      <p>
        共<span>{{total}}</span>条
      </p>
      <!--<j-paging :initPageSize="pageSize" :initPageIndex="pageIdx" :initTotalCount="total"-->
                <!--@pageChanged="query"></j-paging>-->
    </div>
    <j-repulsee @repulse="repulse" @clone="centerDialogVisible = false" :centerDialogVisible="centerDialogVisible" :repulseId="repulseId"></j-repulsee>
  </div>
</template>
<script>
  import { jPaging, jRepulsee } from '../../../components'
  import { dataManage as api } from '../../../api'

  export default {
    name: 'statuteOffice',
    data() {
      return {
        centerDialogVisible: false,
        Authorization: '',
        tableData: [],
        realName: '',
        repulseId: '',
        total: ''
      }
    },
    mounted() {
      this.realName = this.$store.state.user.userInfo.realName
      this.query()
    },
    methods: {
      // 打回确定
      repulse() {
        this.centerDialogVisible = false
        this.query(1)
      },
      // 打回
      unadopt(item) {
        this.centerDialogVisible = true
        this.repulseId = item.id
      },
      // 通过
      adopt(item) {
        this.confirmTxt('是否确定通过').then(res => {
          let params = {}
          let _this = this
          item.status = 3
          params = {
            tableId: item.id,
            status: 3,
            Authorization: this.Authorization
          }
          api
            .report(params)
            .then(rtn => {
              if (rtn.code === 1) {
                _this.cancel()
                console.log('成功修改')
              } else {
                throw new Error('返回数据错误')
              }
            })
            .catch(err => {
              this.pageState = err
            })
        }).catch(res => {})
      },
      query() {
        this.Authorization = localStorage.getItem('accessToken')
        const firstJson = {
          Authorization: this.Authorization,
          operateSpace: '计划处'
        }
        api
          .getHeightNewList(firstJson)
          .then(rtn => {
            this.tableData = rtn.data.list
            this.total = rtn.data.total
          })
          .catch(err => {
            this.pageState = err
          })
      },
      // 查看
      fillReport(id, name, status, endDate) {
        this.$router.push({
          path: '/dataManage/look/planningOfficeReviewDetail',
          query: { id: id, name: name, status: status, lastTime: endDate }
        })
      }
    },
    components: {
      jPaging,
      jRepulsee
    }
  }
</script>
<style scoped lang="scss">
  .dialog-content {
    p {
      padding: 5px 0;
    }
  }
  .table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;
  }

  .text-d {
    text-decoration: underline;
  }

  .paging2 {
    margin-top: 30px;
    & > p {
      float: left;
      font-size: 14px;
      color: #959595;
      position: relative;
      span {
        color: #ff9603;
        margin: 0 3px;
      }
    }
    & > div {
      float: right;
    }
  }
  .stateClass1 {
    color: #a0a0a0;
  }
  .stateClass2 {
    color: #ee8b00;
  }
  .stateClass3 {
    color: #1559ec;
  }
  .stateClass4 {
    color: #ef8b00;
  }
  .stateClass5 {
    color: #28b266;
  }
</style>



