<template>
  <div>
    <div class="table-bor">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column show-overflow-tooltip
          prop="name"
          label="表单名称"
          width="280">
        </el-table-column>
        <el-table-column
          prop="operateSpace"
          label="填报处室"
          width="80">
        </el-table-column>
        <el-table-column
          prop="startDate"
          label="填报开始时间">
          <template slot-scope="scope">
            <span>{{scope.row.startDate | dateFormat}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="endDate"
          label="填报结束时间">
          <template slot-scope="scope">
            <span>--</span>
            <!--<span>{{scope.row.endDate | dateFormat}}</span>-->
          </template>
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.status==0" class="stateClass1">未填报</span>
            <span v-if="scope.row.status==1" class="stateClass2">待上报</span>
            <span v-if="scope.row.status==2" class="stateClass3">待审核</span>
            <span v-if="scope.row.status==4" class="stateClass4">打回-待上报</span>
            <span v-if="scope.row.status==3" class="stateClass5">已完成</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="operation"
          label="操作">
          <template slot-scope="scope">
            <div v-if="scope.row.status==0">
              <el-button @click="fillReport(scope.row)" type="text" size="small" class="p0"><span class="text-d">填报</span></el-button>
            </div>
            <div v-if="scope.row.status==1 || scope.row.status==4">
              <el-button type="text" size="small" @click="report(scope.row)" class="p0"><span class="text-d">上报</span></el-button>
              <el-button type="text" size="small" @click="fillReport(scope.row)" class="p0"><span class="text-d">修改</span></el-button>
              <el-button type="text" size="small" @click="fillReport(scope.row,'look')" class="p0"><span class="text-d">查看</span></el-button>
            </div>
            <div v-if="scope.row.status==2 || scope.row.status==3">
              <el-button type="text" size="small" @click="fillReport(scope.row,'look')" class="p0"><span class="text-d">查看</span></el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="paging2">
      <p>
        共<span>{{total}}</span>条
      </p>
      <!--<j-paging :initPageSize="pageSize" :initPageIndex="pageIdx" :initTotalCount="total"-->
                <!--@pageChanged="query"></j-paging>-->
    </div>
  </div>
</template>
<script>
  import {jPaging} from '../../../components'
  import { dataManage as api } from '../../../api'

  export default {
    name: 'planningOffice',
    data() {
      return {
      //  pageSize: 10,
      //  pageIdx: 1,
        total: 0,
        tableData: []
      }
    },
    mounted() {
      this.query()
    },
    methods: {
      query() {
        const firstJson = {
          operateSpace: '计划处'
        }
        api
          .getFirstPageSelectList(firstJson)
          .then(rtn => {
            this.tableData = rtn.data
            this.total = this.tableData.length
          })
          .catch(err => {
            this.pageState = err
          })
      },
      fillReport(row, str) {
        console.log(row)
        this.$router.push({path: '/dataManage/edit/planningOffice', query: { data: row, id: row.id, op: str }})
      },
      report(row) {
        this.$confirm('是否确认上报?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          customClass: 'messageBox',
          cancelButtonClass: 'is-plain el-button--primary',
          center: true,
          roundButton: true,
          type: 'info'
        }).then(() => {
          const params = {
            tableId: row.id,
            status: 2
          }
          api
            .report(params)
            .then(rtn => {
              this.query()
            })
            .catch(err => {
              this.pageState = err
            })
        }).catch(() => {
        })
      }
    },
    components: {
      jPaging
    }
  }
</script>
<style scoped lang="scss">
  .table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;
  }

  .text-d {
    text-decoration: underline;
  }

  .paging2 {
    margin-top: 30px;
  & > p {
      float: left;
      font-size: 14px;
      color: #959595;
      position: relative;
  span {
    color: #ff9603;
    margin: 0 3px;
  }
  }
  & > div {
      float: right;
    }
  }
  .stateClass1{
    color: #a0a0a0;
  }
  .stateClass2{
    color:#ee8b00;
  }
  .stateClass3{
    color: #1559ec;
  }
  .stateClass4{
    color: #ef8b00;
  }
  .stateClass5{
    color: #28b266;
  }
</style>



