<template>
  <div class="m30 local_view">
    <div class="table-bor">
         <div class="clearFix table-title">
            <span>{{filterData.reportFormName}}</span>
            <el-button type="primary" round plain size="small" class="fr"  @click="exportExcel">报表导出</el-button>
         </div>
        <el-form :inline="true" label-width="80px">
            <el-form-item label="企业名称">
                <el-input size="small" maxlength="100" v-model="filterData.enterpriseName"></el-input>
            </el-form-item>
            <!-- <el-form-item label="区域">
                <el-select placeholder="请选择活动区域"  size="small" v-model="filterData.region">
                  <el-option  label="全部" value="" >全部</el-option>
                  <el-option v-for="item in areasOption" :key="item.value" :label="item.name" :value="item.name"></el-option>
                </el-select>
            </el-form-item> -->
            <el-form-item label="领域大类">
                <el-select placeholder="请选择领域大类"  size="small"  v-model="filterData.areaName">
                  <el-option  label="全部" value="">全部</el-option>
                  <el-option v-for="item in categoriesOption" :key="item.value" :label="item.name" :value="item.name"></el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div class="form-submit">
        <el-button type="primary" size="small" round @click="filterQuery">查询</el-button>
        <el-button type="primary" size="small" round plain @click="clearForm">重置</el-button>
      </div>
        <el-table style="width: 100%;" stripe border :data="tableData">
          <el-table-column prop="enterpriseName" label="企业名称" show-overflow-tooltip width="200" align="left">
            名称
          </el-table-column>
          <el-table-column prop="govcname" label="区域">
            区域
          </el-table-column>
          <el-table-column prop="userEmail" label="领域大类" width="200">
            大类
          </el-table-column>
          <el-table-column prop="enterpriseOutputValue" label="企业产值(万元)">
            企业产值(万元)
          </el-table-column>
          <el-table-column prop="enterpriseBusinessIncome" label="营业收入(万元)">
            营业收入(万元)
          </el-table-column>
          <el-table-column prop="investmentAmount" label="投资额(万元)">
            投资额(万元)
          </el-table-column>
          <el-table-column prop="profitAndTax" label="利税(万元)">
            利税(万元)
          </el-table-column>
          <el-table-column prop="rdInvestment" label="研发投入(万元)">
            研发投入(万元)
          </el-table-column>
      </el-table>
    </div>
    <div class="pagination clearFix">
      <div class="pagination-num">
        共
        <span class="yellow"> {{filterData.total}} </span>条
      </div>
      <div class="pagination-nav">
        <j-paging class="paging" :initPageSize="filterData.pageSize" :initPageIndex="filterData.pageNum" :initTotalCount="filterData.total" @pageChanged="query"></j-paging>
      </div>
    </div>
  </div>
</template>
<script>
  import { dataManage as api } from '../../../api'
  import { jPaging } from '../../../components'
  export default {
    data() {
      return {
        areas: [],
        categories: [],
        tableData: [],
        filterData: {
          pageSize: 10,
          pageNum: 1,
          total: null,
          region: null,
          areaName: null,
          reportFormName: '',
          enterpriseName: ''
        }
      }
    },
    components: {
      jPaging
    },
    methods: {
      exportExcel() {
        let param = this.filterData
        location.href = window.api_host + '/useReportInfo/DetailBycountyExportCopy?enterpriseName=' + param.enterpriseName + '&region=' + param.region + '&areaName=' + param.areaName + '&reportFormName=' + param.reportFormName
      },
      clearForm() {
        this.filterData.pageNum = 1
        this.filterData.areaName = null
        this.filterData.region = null
        this.filterData.enterpriseName = ''
        this.getData()
      },
      getData() {
        api.getDetialByCC(this.filterData).then(res => {
          this.tableData = res.data.list
          this.filterData.pageSize = res.data.pageSize
          this.filterData.pageNum = res.data.pageNum
          this.filterData.total = res.data.total
        })
      },
      query(val) {
        this.filterData.pageNum = val
        this.getData()
      },
      filterQuery() {
        this.filterData.pageNum = 1
        this.getData()
      }
    },
    created() {
      this.filterData.reportFormName = this.$route.params.title
      this.getData()
    },
    computed: {
      areasOption() {
        let option = this.$store.state.areas
        return option.slice(1)
      },
      categoriesOption() {
        return this.$store.state.categories
      }
    }
  }
</script>
<style lang="scss" scoped>
    .local_view{
        .el-form-item{
            margin-right: 30px;
        }
        .form-submit {
          text-align: center;
          margin-bottom: 30px;
          border-bottom: dotted 1px #ccc;
          padding-bottom: 30px;
        }
        .pagination{
          margin-top: 30px;
          width: 100%;
          height:40px;
          .pagination-num{
            float: left;
            .yellow{
              color:#ff9603;
              font-weight:bold
            }
          }
          .pagination-nav{
            float: right;
          }
        }
    }


    .m30 {
    margin: 30px;
  }
  .mr20 {
    margin-right: 20px;
  }
  .heigh_theme {
    margin-bottom: 20px;
    li {
      float: left;
      padding-right: 40px;
      height: 30px;
      line-height: 30px;
    }
  }
  .table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;

    .table-title {
      padding-bottom: 20px;
      margin-bottom: 10px;
      text-align: center;

      span {
        line-height: 32px;
        font-size: 16px;
        color: #333;
        font-weight: bold;
      }
    }
  }
  .submit-box {
    text-align: center;
    margin-top: 30px;
  }
  .stateClass1{
    color: #a0a0a0;
  }
  .stateClass2{
    color:#ee8b00;
  }
  .stateClass3{
    color: #1559ec;
  }
  .stateClass4{
    color: #ef8b00;
  }
  .stateClass5{
    color: #28b266;
  }
  .mar70{
    margin-left: 70px;
  }
  /deep/ .el-table thead.is-group th{
    border-right:solid 1px #fff;
  }
  /deep/ .el-table th{
    border-bottom:solid 1px #fff;
  }

</style>



