<template>
  <div class="m30">
    <div class="table-bor">
      <div class="clearFix form-submit table-title">
        <span>{{ name }}</span>
        <el-button type="primary" round plain size="small" class="fr" @click="exportExcel">报表导出</el-button>
      </div>
      <div class="heigh_theme clearFix">
        <ul>
          <li>状态：&nbsp;&nbsp;
            <span v-if="theme.status==0" class="stateClass1">未填报</span>
            <span v-if="theme.status==1" class="stateClass2">待上报</span>
            <span v-if="theme.status==2" class="stateClass3">待审核</span>
            <span v-if="theme.status==3" class="stateClass5">已完成</span>
            <span v-if="theme.status==4" class="stateClass4">打回-待上报</span>
          </li>
          <li v-if="this.theme.status==2" class="mar70">最新上报时间：&nbsp;&nbsp;<span class='en-report-count'>{{theme.time | dateFormat}}</span></li>
          <li v-if="this.theme.status==3" class="mar70">完成时间：&nbsp;&nbsp;<span class='en-report-count'>{{theme.time | dateFormat}}</span></li>
        </ul>
      </div>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column prop="county" label="县（市）区">
          <template slot-scope="scope">{{scope.row[0]}}</template>
        </el-table-column>
        <el-table-column label="创新要素" align="center">
          <el-table-column prop="other" label="研发投入占GDP比重（%，年度数据）">
            <template slot-scope="scope">
              <span>{{scope.row[1] | numLine}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="申报（获批）省级以上项目数（个）">
            <template slot-scope="scope">
              <span>{{scope.row[2] | numLine}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="申报（获批）省级以上资金数（万元）">
            <template slot-scope="scope">
              <span>{{scope.row[3] | numLine}}</span>
            </template>
          </el-table-column>
        </el-table-column>

      </el-table>
    </div>
    <div class="form-submit submit-box">
      <el-button type="primary" round size="small" @click="adopt" v-if="theme.status == 2">通过</el-button>
      <el-button type="warning" round size="small" @click="unadopt" v-if="theme.status == 2">打回</el-button>
      <el-button type="primary" round plain size="small" @click="cancel">返回</el-button>
    </div>
    <j-repulsee @repulse="repulse" @clone="centerDialogVisible = false" :centerDialogVisible="centerDialogVisible" :repulseId="repulseId"></j-repulsee>
  </div>
</template>
<script>
  import { dataManage as api } from '../../../api/index'
  import { jRepulsee } from '../../../components'
  export default {
    name: 'editHighAndOffice',
    data() {
      return {
        centerDialogVisible: false,
        theme: {
          status: 0,
          time: '--'
        },
        tableData: [],
        baseUrl: '',
        id: '',
        relativeId: '',
        flag: '',
        name: '',
        Authorization: '',
        realName: this.$store.state.user.userInfo.realName,
        repulseId: ''
      }
    },
    mounted() {
      console.log(this.$route.query)
      this.Authorization = localStorage.getItem('accessToken')
      this.theme.status = Number(this.$route.query.status)
      this.name = this.$route.query.name
      this.theme.time = this.$route.query.lastTime
      this.id = this.$route.query.id
      this.baseUrl = window.api_host
      this.queryDetial()
    },
    methods: {
      // 打回确定
      repulse() {
        this.centerDialogVisible = false
        this.$router.push('/dataManage/officeReview/planningOfficeReview')
      },
      // 打回
      unadopt() {
        this.centerDialogVisible = true
        this.repulseId = this.id
      },
      //  通过
      adopt() {
        this.confirmTxt('是否确定通过').then(res => {
          let that = this
          let params = {tableId: that.id, status: 3, optUser: that.realName}
          api.report(params).then(res => {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-icon-correct"></i><span>' + res.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {
                this.$router.push('/dataManage/officeReview/planningOfficeReview')
              }
            })
          }).catch(err => {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {}
            })
          })
        }).catch(res => {})
      },
      queryDetial() {
        api
          .getDetialTable({
            id: this.$route.query.id,
            Authorization: this.Authorization
          })
          .then(rtn => {
            if (rtn.data === '') {
              this.relativeId = this.id
              this.flag = 0
            } else {
              let rtnData = JSON.parse(rtn.data.data)
              this.relativeId = rtn.data.relativeId
              this.tableData = rtnData
              this.flag = 1
            }
          })
          .catch(err => {
            this.pageState = err
          })
      },
      //  报表导出
      exportExcel() {
        location.href =
          this.baseUrl + '/dataUpLoadController/exportExcel?id=' + this.relativeId
      },
      cancel() {
        // this.$router.push('/dataManage/fillingOffice/highAndOffice')
        history.go(-1)
      }
    },
    components: {
      jRepulsee
    }
  }
</script>
<style scoped lang="scss">
  .m30 {
    margin: 30px;
  }
  .mr20 {
    margin-right: 20px;
  }
  .heigh_theme {
    margin-bottom: 20px;
    li {
      float: left;
      padding-right: 40px;
      height: 30px;
      line-height: 30px;
    }
  }
  .table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;

    .table-title {
      padding-bottom: 20px;
      margin-bottom: 10px;
      text-align: center;

      span {
        line-height: 32px;
        font-size: 16px;
        color: #333;
        font-weight: bold;
      }
    }
  }
  .submit-box {
    text-align: center;
    margin-top: 30px;
  }
  .stateClass1{
    color: #a0a0a0;
  }
  .stateClass2{
    color:#ee8b00;
  }
  .stateClass3{
    color: #1559ec;
  }
  .stateClass4{
    color: #ef8b00;
  }
  .stateClass5{
    color: #28b266;
  }
  .mar70{
    margin-left: 70px;
  }
  /deep/ .el-table thead.is-group th{
    border-right:solid 1px #fff;
  }
  /deep/ .el-table th{
    border-bottom:solid 1px #fff;
  }
</style>



