<template>
  <div class="rg-r">
    <div class="table-bor">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
        show-overflow-tooltip
          prop="name"
          label="表单名称"
          width="280">
        </el-table-column>
        <el-table-column
          prop="operateSpace"
          label="填报处室"
          width="80">
        </el-table-column>
        <el-table-column
          prop="startTime"
          label="填报开始时间">
          <template slot-scope="scope">
            <span>{{scope.row.startDate | dateFormat}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="endTime"
          label="填报结束时间">
          <template slot-scope="scope">
            <span>--</span>
            <!--<span>{{scope.row.endDate | dateFormat}}</span>-->
          </template>
        </el-table-column>
        <el-table-column
          label="状态"
          width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.status==0" class="stateClass1">未填报</span>
            <span v-if="scope.row.status==1" class="stateClass2">待上报</span>
            <span v-if="scope.row.status==2" class="stateClass3">待审核</span>
            <span v-if="scope.row.status==3" class="stateClass5">已完成</span>
            <span v-if="scope.row.status==4" class="stateClass4">打回-待上报</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <div v-if="scope.row.status==0 || scope.row.status==4 || scope.row.status==1">
              -
            </div>
            <div v-if="scope.row.status==2">
              <el-button type="text" size="small"><span class="text-d" @click="pass(scope.row.id,scope.$index,3)">通过</span></el-button>
              <el-button type="text" size="small"><span class="text-d" @click="frame(scope.row.id)">打回</span></el-button>
              <el-button type="text" size="small" @click="goDetail(scope.row.id,scope.row.name,scope.row.status,scope.row.lastTime)"><span class="text-d">查看</span></el-button>
            </div>
            <div v-if="scope.row.status==3">
              <el-button type="text" size="small" @click="goDetail(scope.row.id,scope.row.name,scope.row.status,scope.row.lastTime)"><span class="text-d">查看</span></el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="paging2">
      <p>
        共<span>{{filterData.total}}</span>条
      </p>
      <j-paging :initPageSize="filterData.pageSize" :initPageIndex="filterData.pageNum" :initTotalCount="filterData.total"
                @pageChanged="query"></j-paging>
    </div>
    <j-repulsee @repulse="repulse" @clone="centerDialogVisible = false" :centerDialogVisible="centerDialogVisible" :repulseId="repulseId"></j-repulsee>
  </div>
</template>
<script>
  import {jPaging, jRepulsee} from '../../../components'
  import { dataManage as api } from '../../../api'
  export default {
    name: 'highAndOffice',
    data() {
      return {
        centerDialogVisible: false,
        realName: '',
        repulseId: '',
        tableData: [],
        filterData: {
          pageSize: 10,
          pageNum: 1,
          total: null,
          operateSpace: '知产处'
        }
      }
    },
    methods: {
      pass(id, idx, stu) {
        this.confirmTxt('是否确定通过').then(res => {
          let that = this
          let params = {tableId: id, status: 3, optUser: that.realName}
          api.report(params).then(res => {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-icon-correct"></i><span>' + res.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {
                that.tableData[idx].status = stu
              }
            })
          }).catch(err => {
            that.$msgbox({
              title: '系统提示',
              message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
              center: true,
              roundButton: true,
              customClass: 'messageBox',
              type: 'success',
              dangerouslyUseHTMLString: true,
              callback: () => {}
            })
          })
        }).catch(res => {})
      },
      frame(id) {
        this.repulseId = id
        this.centerDialogVisible = true
      },
      repulse() {
        this.centerDialogVisible = false
        this.query(1)
      },
      goDetail(idx, nme, stu, ent) {
        this.$router.push({
          path: '/dataManage/look/knowledgedetail',
          query: {id: idx, name: nme, status: stu, lastTime: ent}
        })
      },
      getData() {
        let that = this
        api.getSelectList(that.filterData).then(res => {
          let jData = res.data
          that.tableData = jData.list
          that.filterData.pageSize = jData.pageSize
          that.filterData.pageNum = jData.pageNum
          that.filterData.total = jData.total
        }).catch(err => {
          that.$msgbox({
            title: '系统提示',
            message: '<i class="iconfont icon-cuowu"></i><span>' + err.msg + '</span>',
            center: true,
            roundButton: true,
            customClass: 'messageBox',
            type: 'success',
            dangerouslyUseHTMLString: true,
            callback: () => {}
          })
        })
      },
      query(page) {
        this.filterData.pageNum = page
        this.getData()
      },
      fillReport(row) {
      }
    },
    components: {
      jPaging,
      jRepulsee
    },
    mounted() {
      this.realName = this.$store.state.user.userInfo.realName
      this.getData()
    }
  }
</script>
<style scoped lang="scss">
.rg-r{
  .table-bor {
    border-radius: 5px 5px 0 0;
    overflow: hidden;
  }

  .text-d {
    text-decoration: underline;
  }

  .paging2 {
    margin-top: 30px;
    & > p {
      float: left;
      font-size: 14px;
      color: #959595;
      position: relative;
      span {
        color: #ff9603;
        margin: 0 3px;
      }
    }
    & > div {
      float: right;
    }
  }
  .stateClass1{
    color: #a0a0a0;
  }
  .stateClass2{
    color:#ee8b00;
  }
  .stateClass3{
    color: #1559ec;
  }
  .stateClass4{
    color: #ef8b00;
  }
  .stateClass5{
    color: #28b266;
  }
  table{
      tr{
        td{
          a,.el-button{
            color:#409EFF;
            text-decoration:underline;
            padding:0
          }
          span{
            &.info{color:#9d9d9d}
            &.warning{color:#f2aa48}
            &.primary{color:#1559ec}
            &.success{color:#45bc7a}
          }
        }
      }
    }
  }
</style>



