<template>
  <div>
    <div class="header">
      <el-form :inline="true" label-width="100px">
        <el-form-item label="填报开始时间">
          <el-col :span="11">
            <el-date-picker
              type="date" placeholder="选择日期"
              :picker-options="pickerBeginDateBefore"
              style="width: 150px"
              size="small"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="filterData.qStartTime"
            ></el-date-picker>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-date-picker
              type="date"
              size="small"
              :picker-options="pickerBeginDateAfter"
              placeholder="选择日期"
              style="width: 150px;"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="filterData.qEndTime"
            ></el-date-picker>
          </el-col>
        </el-form-item>
      </el-form>
      <div class="form-submit">
        <el-button type="primary" size="small" round @click="queryList">查询</el-button>
        <el-button type="primary" size="small" round plain @click="clear">重置</el-button>
      </div>
    </div>
    <div class="con">
      <div class="table-bor" v-loading='loading'>
        <el-table
          :data="tableData"
          style="width: 100%">
          <el-table-column
            prop="reportFormName"
            label="表单名称"
            width="260" :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <span>{{scope.row.reportFormName | numLine}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="unreport"
            label="未上报企业数"
            width="220" :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <span>{{scope.row.unreport | numLine}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="creatTime"
            label="填报开始时间">
            <template slot-scope="scope">
              <span>{{scope.row.creatTime | dateFormat}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="endTime"
            label="填报结束时间">
            <template slot-scope="scope">
              <span>--</span>
              <!--<span>{{scope.row.endTime | dateFormat}}</span>-->
            </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label="操作">
            <template slot-scope="scope">
              <router-link :to="{path: '/dataManage/edit/districtSummaryDetail', query: {name: scope.row.reportFormName}}">
                <el-button type="text" size="small"><span class="text-d">查看</span></el-button>
              </router-link>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="paging2">
        <p>
          共<span>{{filterData.total}}</span>条
        </p>
        <j-paging class="paging" :initPageSize="filterData.pageSize" :initPageIndex="filterData.pageNum" :initTotalCount="filterData.total" @pageChanged="query"></j-paging>
      </div>
    </div>
  </div>
</template>
<script>
  import {jPaging} from '../../../components'
  import { dataManage as api } from '../../../api'
  export default {
    name: '',
    data() {
      return {
        filterData: {
          pageSize: 10,
          pageNum: 1,
          total: null,
          qStartTime: '',
          qEndTime: ''
        },
        loading: false,
        tableData: [],
        pickerBeginDateBefore: {
          disabledDate: (time) => {
            let beginDateVal = this.filterData.qEndTime
            if (beginDateVal) {
              return time.getTime() > new Date(beginDateVal)
            } else {
              return time.getTime() > Date.now()
            }
          }
        },
        pickerBeginDateAfter: {
          disabledDate: (time) => {
            let beginDateVal = this.filterData.qStartTime
            if (beginDateVal) {
              return time.getTime() < new Date(beginDateVal) || time.getTime() > Date.now()
            } else {
              return time.getTime() > Date.now()
            }
          }
        }
      }
    },
    methods: {
      //  查询
      queryList() {
        this.filterData.pageNum = 1
        this.query()
      },
      //  重置
      clear() {
        this.filterData.qStartTime = ''
        this.filterData.qEndTime = ''
        this.query(1)
      },
      getData() {
        this.loading = true
        api
          .getTotalListByCounty(this.filterData)
          .then(rtn => {
            this.loading = false
            this.tableData = rtn.data.list
            this.filterData.total = rtn.data.total
            this.filterData.pageSize = rtn.data.pageSize
            this.filterData.pageNum = rtn.data.pageNum
          })
          .catch(err => {
            this.loading = false
            this.pageState = err
          })
      },
      query(val) {
        let that = this
        that.filterData.pageNum = val
        that.getData()
      }
    },
    components: {
      jPaging
    },
    mounted() {
      if (localStorage.filterData) {
        this.filterData = JSON.parse(localStorage.filterData)
      }
      this.getData()
    },
    beforeRouteLeave(to, from, next) {
      if (to.name === 'districtSummaryDetail') {
        localStorage.filterData = JSON.stringify(this.filterData)
        from.meta.keepAlive = true
      } else {
        localStorage.removeItem('filterData')
        from.meta.keepAlive = false
      }
      next()
    }
  }
</script>
<style scoped lang="scss">
  .header{
    width: 100%;
    position: relative;
  }
  .el-form-item{
    margin-right: 80px;
  }
  /** .form-submit {
    position: absolute;
    right: 0;
    bottom: 30px;
    .el-button--small, .el-button--small.is-round{
      padding: 9px 30px;
    }
  } **/
  .form-submit {
    text-align: center;
    margin-bottom: 30px;
    border-bottom: dotted 1px #ccc;
    padding-bottom: 30px;
  }
  .con{
    margin-top: 10px;
    .table-bor {
      border-radius: 5px 5px 0 0;
      overflow: hidden;
    }

    .text-d {
      text-decoration: underline;
    }

    .paging2 {
      margin-top: 30px;
      & > p {
        float: left;
        font-size: 14px;
        color: #959595;
        position: relative;
        span {
          color: #ff9603;
          margin: 0 3px;
        }
      }
      & > div {
        float: right;
      }
    }
  }
</style>



