<template>
  <div class="m30">
    <div class="table-bor">
      <div class="clearFix form-submit table-title">
        <span>{{ name }}</span>
        <el-button type="primary" round plain size="small" class="fr" @click="exportExcel">报表导出</el-button>
        <el-upload :show-file-list="false" class="fr mr20"  v-show="showBtn"
                   :action="baseUrl+'/dataUpLoadController/importExcel'"
                   :on-success="(res)=>{return importExcel(res)}">
          <el-button type="primary" round plain size="small">报表导入</el-button>
        </el-upload>
      </div>
      <jBackList :backlist="backList"></jBackList>
       <div class="heigh_theme" v-if="theme.themeShow">
          <ul>
            <li v-if ='theme.status===3'><span>状态：</span>已完成</li>
            <li v-if ='theme.status===2'><span>状态：</span>待审核</li>
            <li><span>最新上报时间：</span>{{theme.time}}</li>
        </ul>
        </div>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column prop="county" label="县（市）区">
          <template slot-scope="scope">{{scope.row[0]}}</template>
        </el-table-column>
        <el-table-column label="产业发展" align="center">
          <el-table-column prop="other" label="传统产业企业研发机构建设率(%)" width="180">
            <template slot-scope="scope">
              <!-- <el-input v-model="scope.row[1]"></el-input> -->
              <span v-if="!showBtn">{{scope.row[1]}}</span>
              <el-input v-model="scope.row[1]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="平台载体" align="center">
          <el-table-column prop="other" label="省级以上科技创新平台数量(家)" width="180">
            <template slot-scope="scope">
              <!-- <el-input v-model="scope.row[2]"></el-input> -->
              <span v-if="!showBtn">{{scope.row[2]}}</span>
              <el-input v-model="scope.row[2]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="省级以上科技企业孵化器数量(家)" width="180">
            <template slot-scope="scope">
              <!-- <el-input v-model="scope.row[3]"></el-input> -->
              <span v-if="!showBtn">{{scope.row[3]}}</span>
              <el-input v-model="scope.row[3]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="省级以上科技企业孵化器在孵企业数(家)" width="180">
            <template slot-scope="scope">
              <!-- <el-input v-model="scope.row[4]"></el-input> -->
              <span v-if="!showBtn">{{scope.row[4]}}</span>
              <el-input v-model="scope.row[4]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="省级以上科技企业孵化器孵化面积(cm2)" width="180">
            <template slot-scope="scope">
              <!-- <el-input v-model="scope.row[5]"></el-input> -->
              <span v-if="!showBtn">{{scope.row[5]}}</span>
              <el-input v-model="scope.row[5]" v-else></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="省级以上众创空间数量(家)" width="180">
            <template slot-scope="scope">
              <!-- <el-input v-model="scope.row[6]"></el-input> -->
              <span v-if="!showBtn">{{scope.row[6]}}</span>
              <el-input v-model="scope.row[6]" v-else></el-input>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
    <div class="form-submit submit-box" v-if="showBtn">
      <el-button type="primary" round size="small" @click="save">保存</el-button>
      <el-button type="primary" round plain size="small" @click="cancel">取消</el-button>
    </div>
    <div class="form-submit submit-box" v-else>
      <el-button type="primary" round plain size="small" @click="goBack">返回</el-button>
    </div>
    <!-- <div class="form-submit submit-box" v-show="theme.adoptFlags">
      <el-button type="primary" round size="small" @click="adopt">通过</el-button>
      <el-button type="primary" round size="small" @click="unadopt">打回</el-button>
      <el-button type="primary" round plain size="small" @click="cancel">取消</el-button>
    </div> -->
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div class="dialog-content">
        <p>请输入原因</p>
        <textarea name="area1"  cols="40" rows="5" v-model='unadoptData.reason'></textarea>
        <p>操作人:<span>{{unadoptData.operator}}</span></p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sureAdpt()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { dataManage as api } from '../../../api'
import { jBackList } from '../../../components'
export default {
  name: 'editFillCondition',
  data() {
    return {
      backList: [],
      unadoptData: {
        reason: '',
        status: 4,
        optUser: 'admin'
      },
      centerDialogVisible: false,
      repulse: false,
      theme: {
        status: '',
        time: '2018-02-07 10:09:10',
        adoptFlags: false,
        themeShow: false
      },
      Authorization: '',
      tableData: [],
      params: [],
      baseUrl: '',
      id: '',
      relativeId: '',
      flag: '',
      showBtn: true,
      name: ''
    }
  },
  mounted() {
    this.Authorization = localStorage.getItem('accessToken')
    this.theme.status = this.$route.query.status
    if (this.$route.query.status === 3) {
      this.theme.adoptFlags = false
    } else if (this.$route.query.status === 2) {
      this.theme.adoptFlags = true
    }
    this.params = this.$route.query
    this.name = this.$route.query.name
    this.baseUrl = window.api_host
    if (this.$route.query.op === 'look') {
      this.showBtn = false
      this.query()
    } else if (this.$route.query.op === 'examine') {
      this.showBtn = false
      this.theme.themeShow = true
      this.queryDetial()
    } else {
      this.query()
    }
    this.getBackList()
  },
  methods: {
    sureAdpt() {
      this.centerDialogVisible = false
      this.repulse = true
    },
    adopt() {
      let params = {
        tableId: this.params.id,
        status: 3,
        Authorization: this.Authorization
      }
      let _this = this
      api
        .report(params)
        .then(rtn => {
          if (rtn.code === 1) {
            _this.cancel()
            console.log('成功修改')
          } else {
            throw new Error('返回数据错误')
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    unadopt() {
      this.centerDialogVisible = true
      this.unadoptData.tableId = this.params.id
      this.unadoptData.Authorization = this.Authorization
    },
    queryDetial() {
      api
        .getDetialTable({
          id: this.$route.query.id,
          Authorization: this.Authorization
        })
        .then(rtn => {
          if (rtn.data === '') {
            this.id = 0
            this.relativeId = this.params.id
            this.flag = 0
          } else {
            let rtnData = JSON.parse(rtn.data.data)
            this.id = rtn.data.id
            this.relativeId = rtn.data.relativeId
            this.tableData = rtnData
            this.flag = 1
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    query() {
      api
        .getEditTable(this.params)
        .then(rtn => {
          if (rtn.data === '') {
            this.id = 0
            this.relativeId = this.params.id
            this.flag = 0
          } else {
            let rtnData = JSON.parse(rtn.data.data)
            this.id = rtn.data.id
            this.relativeId = rtn.data.relativeId
            this.tableData = rtnData
            this.flag = 1
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    importExcel(res) {
      this.tableData = res.data
    },
    exportExcel() {
      location.href =
        this.baseUrl + '/dataUpLoadController/exportExcel?id=' + this.relativeId
    },
    save() {
      let that = this
      if (this.tableData.length === 0) {
        this.$message.error('请导入报表后再提交！')
        return
      }
      let arr = JSON.stringify(this.tableData)
      console.log(arr)
      let params = {
        relateid: this.relativeId,
        id: this.id,
        data: arr,
        flag: this.flag
      }
      api
        .saveExcel(params)
        .then(rtn => {
          if (rtn.code === 1) {
            that.cancel()
          }
        })
        .catch(err => {
          this.pageState = err
        })
    },
    cancel() {
      // this.$router.push('/dataManage/fillingOffice/fillCondition')
      history.go(-1)
    },
    goBack() {
      history.go(-1)
    },
    getBackList() {
      let params = {tableId: this.$route.query.id}
      api.getReportList(params).then(res => {
        this.backList = res.data.list
      }).catch(res => {
      })
    }
  },
  watch: {
    repulse(oldValue, newVal) {
      if (oldValue) {
        api
          .report(this.unadoptData)
          .then(rtn => {
            if (rtn.code === 1) {
              this.cancel()
              console.log('成功修改')
            } else {
              throw new Error('返回数据错误')
            }
          })
          .catch(err => {
            this.pageState = err
          })
      }
    }
  },
  components: {
    jBackList
  }
}
</script>
<style scoped lang="scss">
.back-info{
  padding:30px 130px;
  background: #edf3ff;
  margin-bottom: 10px;
  &:last-child{
    margin-bottom:30px;
  }
  .i-tit{
    font-weight: bolder;
    margin-bottom: 20px;
    word-break:break-all

  }
  .i-time{
    text-align: right;
    color:#7c7d7d
  }
}
.heigh_theme {
  li {
    float: left;
    padding-right: 40px;
    height: 30px;
    line-height: 30px;
  }
}
.m30 {
  margin: 30px;
}
.mr20 {
  margin-right: 20px;
}
.mr20 {
  margin-right: 20px;
}
.table-bor {
  border-radius: 5px 5px 0 0;
  overflow: hidden;

  .table-title {
    padding-bottom: 20px;
    margin-bottom: 10px;
    text-align: center;

    span {
      line-height: 32px;
      font-size: 16px;
      color: #333;
    }
  }
}
.submit-box {
  text-align: center;
  margin-top: 30px;
}
/deep/ .el-table thead.is-group th{
    border-right:solid 1px #fff;
  }
  /deep/ .el-table th{
    border-bottom:solid 1px #fff;
  }
</style>



